.slugdataimages {
  position: relative; }
  .slugdataimages img {
    margin: 5px; }
  .slugdataimages img:hover {
    cursor: pointer; }

.slugdataimages:hover .produt_hover_btn {
  display: block !important;
  position: absolute;
  top: -6px;
  right: 0;
  border: none;
  color: black;
  background-color: white; }

.slugdataimages:hover .delete12:hover {
  color: red;
  text-shadow: 0px 0px 10px red; }

.slugdataimages:hover .delete12:active {
  color: rgba(255, 0, 0, 0.507);
  text-shadow: 0px 0px 10px red; }

.btn-edit {
  background-color: #f3e13b; }

.btn {
  font-size: 14px; }



@media screen and (min-width: 1201px){
    .Cards-subcategory{
        grid-template-columns: auto auto auto auto;
        margin: auto;
    }
}
@media screen and (min-width: 992px) and (max-width: 1200px){
    .Cards-subcategory{
        grid-template-columns: 30% 30% 30%;
        margin: auto;
    }
}
@media screen and (min-width: 571px) and (max-width: 991px) {
    .Cards-subcategory{
        grid-template-columns: 50% 50%;
        margin: auto;
    }
    .Dashboard{
        display: grid !important;
        grid-template-columns: 50% 50% !important;
    }
}
@media screen and (max-width: 570px) {
    .Cards-subcategory{
        grid-template-columns: 100%;
        margin: auto;
    }   
    .Dashboard{
        display: grid !important;
        grid-template-columns: 100% !important;
    }
}
